
.page{transition:400ms;transition-property:transform
}
.with-panel-left-reveal .page{transform:translate3d(12rem, 0, 0)
}
.with-panel-left-reveal .panel-reveal{left:0
}
.with-panel-right-reveal .page{transform:translate3d(-12rem, 0, 0)
}
.with-panel-right-reveal .panel-reveal{right:0
}


