
.ms-swiper{width:100%;height:16rem;overflow:hidden;background-color:#fff;position:relative
}
.ms-swiper ul{padding:0;margin:0;list-style:none;overflow:hidden
}
.ms-swiper .li-img{float:left;height:100%;overflow:hidden
}
.ms-swiper .li-img a{display:block
}
.ms-swiper .li-img img{max-width:100%;max-height:100%;display:block;margin:0 auto
}
.ms-swiper .dots{text-align:center;margin-top:-1rem;z-index:5;position:absolute;width:100%
}
.ms-swiper .dots li{display:inline-block;width:.5rem;height:.5rem;border-radius:.25rem;background-color:#000;margin:0 .2rem;opacity:.2
}
.ms-swiper .dots li.active{background-color:#007aff;opacity:1
}


